.MatchPage {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-column-gap: 40px;
    grid-row-gap: 20px;
    
    .match-header {
         margin-bottom:20px;
         margin-top:20px;
    }
    
    .select-year{
        font-size: 2rem;
        margin-top: 30px;
    }

    .matchDataNull{
        margin: 20px;
        color: red;
    }
}

