.HomePage{
    .home-page-header{
        text-align: center;
        margin: 20px;
        font-size: 2rem;

    }

    .dev-info {
        height: 50px;
        width: 50px;
        border-color: white;
    }
    .home-body{
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding: 20px;
        grid-column-gap: 10px;
        grid-row-gap: 10px;
     }
}

