.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .team-name-section {
        grid-column: span 3;
        justify-self: center;
        align-self: end;
    }
    .team-name {
        font-size: 4rem;
    }

    .win-loss-section{
        justify-self: center;
        text-align: center;
        padding: 10px;
    }

    .detailed-data-section{
        grid-column: span 4;
    }

    .more-info{
       align-self: center;
       justify-self: center;
       font-size: 1.5rem;
    }
}

