.DetailedTeamData {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px;
    margin-bottom: 10px;
   
    .other-team, .venue, .date {
        margin-bottom: 10px;
    }
    .additional-info {
        text-align: right;
        & > p {
            margin-bottom: 20px;;
        }
    }
}

.won {
    background-color: #47d147;
}
.loss {
    background-color: #ff4d4d;
}